import { Component, OnInit } from '@angular/core';
import { Validators, AbstractControl, FormControl, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertService, AuthService } from 'src/app/core/services';
import { RegisterationRequest } from 'src/app/modules/auth/models/register-request.model';
import { UserData } from 'src/app/modules/auth/models/userData.model';
import { UserType } from 'src/app/modules/auth/models/userType.enum';
import { AuthApiService } from 'src/app/modules/auth/services/api/auth-api.service';
import { ProfileService } from '../../services/profile.service';

@Component({
  selector: 'app-profileedit',
  templateUrl: './profileedit.component.html',
  styleUrls: ['./profileedit.component.scss', '../../../auth/components/register/auth.style.scss']
})
export class ProfileeditComponent implements OnInit {
  myForm;
  userData: UserData;
  languages = [
    {id: 1, name: 'English'},
    {id: 2, name: 'French'},
    {id: 3, name: 'German'},
  ]
  gender = [
    {id: 1, name: 'Mr'},
    {id: 2, name: 'Mrs'},
  ]
  userInfo;
  activeBusiness= true;
  personalRoles;
  userType = '';
  get userNameCtrl() { return this.myForm.get('userName') }
  get firstNameCtrl() { return this.myForm.get('firstName') }
  get lastNameCtrl() { return this.myForm.get('lastName') }
  get mobileNumberCtrl() { return this.myForm.get('mobileNumber') }
  get emailCtrl() { return this.myForm.get('email') }
  get passwordCtrl() { return this.myForm.get('password') }
  get confirmPasswordCtrl () { return this.myForm.get('confirmPassword') }
  get policyAgreementCtrl() { return this.myForm.get('policyAgreement') }
  constructor(
    private authApiService:AuthApiService,
    private alertSrvc:AlertService,
    private authSrvc: AuthService,
    private router:Router,
    private fb : FormBuilder,
    private profileServ: ProfileService,
  ) { }

  ngOnInit(): void {
    // this.createNormalUserForm();
    this.authSrvc.userData$.subscribe((data) => {
      if (data) this.userData = data;
    });
    this.getUserData()
  }


  createNormalUserForm(userInfo) {
    this.myForm = this.fb.group(
      {
        language:  new FormControl(userInfo ? userInfo.language : '', [Validators.required]),
        telefonNumber:  new FormControl(userInfo ? userInfo.telefonNumber : '', [Validators.required]),
        phoneNumber:  new FormControl(userInfo ? userInfo.phoneNumber : '', [Validators.required]),
        faxNumber:  new FormControl(userInfo ? userInfo.faxNumber : ''),
        firstName: [
          userInfo ? userInfo.firstName : '',
          [Validators.required,
            this.noWhitespaceValidator,
          ],
        ],
        lastName: [
          userInfo ? userInfo.lastName : '',
          [Validators.required,
            this.noWhitespaceValidator,
          ],
        ],
      title:  new FormControl(userInfo ? userInfo.title : '', [Validators.required]),


      },
      // { validator: this.passwordMatchValidator }
      )
  }

  getUserData() {
    this.profileServ.getUserData(this.userData.id)
    .subscribe((res) => {
      this.userInfo = res.data
      console.log(res.data, 'edit');
      this.personalRoles = res.data.roles
      if(this.personalRoles.includes('Basic') && !this.personalRoles.includes('Business')) {
        this.activeBusiness = true;
        this.userType = "Basic";
        this.createNormalUserForm(this.userInfo);
      } else {
        this.activeBusiness = false
        this.userType = "Business";
        this.createBusinessUserForm(this.userInfo);
      }
    })
  }
  createBusinessUserForm(userInfo) {
    this.myForm = this.fb.group(
      {
        companyName: new FormControl(userInfo ? userInfo.companyName : '', [Validators.required]),
        commercialName: new FormControl(userInfo ? userInfo.commercialName : '', [Validators.required]),
        language:  new FormControl(userInfo ? userInfo.language : '', [Validators.required]),
        telefonNumber:  new FormControl(userInfo ? userInfo.telefonNumber : '', [Validators.required]),
        phoneNumber:  new FormControl(userInfo ? userInfo.phoneNumber : '', [Validators.required]),
        faxNumber:  new FormControl(userInfo ? userInfo.faxNumber : ''),

        firstName: [
          userInfo ? userInfo.firstName : '',
          [Validators.required,
            this.noWhitespaceValidator,
          ],
        ],
        lastName: [
          userInfo ? userInfo.lastName : '',
          [Validators.required,
            this.noWhitespaceValidator,
          ],
        ],
        contactTelefonNumber: new FormControl(userInfo ? userInfo.contactTelefonNumber : '', [Validators.required]),
      contactPhoneNumber:  new FormControl(userInfo ? userInfo.contactPhoneNumber : '', [Validators.required]),
      contactFaxNumber:  new FormControl(userInfo ? userInfo.contactFaxNumber : ''),
      contactLanguge:  new FormControl(userInfo ? userInfo.contactLanguge : '', [Validators.required]),
      contactEmail:  new FormControl(userInfo ? userInfo.contactEmail : '', {validators : [Validators.required, Validators.email]}),
      title:  new FormControl(userInfo ? userInfo.title : '', [Validators.required]),


      },
      )
  }


  /* Function Name : noWhitespaceValidator
   * Author :
   * Created Date : 10-01-2019
   * Modified Date : *
   * Purpose : to validate whitespace of  text field
   * PARAMS : control
   */

  public noWhitespaceValidator(control: FormControl) {
    if (control.value) {
      const isWhitespace = (control.value || "").trim().length === 0;
      const isValid = !isWhitespace;
      return isValid ? null : { whitespace: true };
    }
  }


  updateData(mydata) {
    if (this.myForm.invalid) {
      this.myForm.markAllAsTouched()
      this.alertSrvc.error('Complete all the required fields(*) first')
    } else {
      console.log(mydata);
      let formValue = mydata.value
      formValue.id = this.userData.id
      if(this.userType == 'Basic') {
        this.profileServ.updateBasicUser(this.userData.id,formValue)
        .subscribe((res) => {
          // console.log(res);
          this.alertSrvc.success('Your Data updated SuccessFully')
          this.router.navigate(['/profile',{searchParam: 'personal'}])
        })
      } else {
        this.profileServ.updateBusinessUser(this.userData.id,formValue)
        .subscribe((res) => {
          // console.log(res);
          this.alertSrvc.success('Your Data updated SuccessFully')
          this.router.navigate(['/profile',{searchParam: 'personal'}])
        })
      }
    }
  }



}
